@import "styles/mixins";
@import "styles/variables";

.wrapper {
  padding-top: 1rem;
}

.sliderWrapper {
  position: relative;
  padding-top: 15.5rem;
  padding-bottom: 2.5rem;
  background-image: url("/assets/images/principles.jpg");
  background-size: cover;
}

.slider {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 35rem;
    height: 100%;
    background-color: $colorHighlight;
    border-radius: 0 3rem 3rem 0;
    z-index: 1;
  }
}

.list {
  @include resetList
}

.item {
  padding: 3rem 4rem 2.5rem 6rem;
  width: 35rem;
  border-radius: 0 3rem 3rem 0;
}

.heading,
.description {
  position: relative;
  z-index: 3;
}

.description {
  @include regular;

  margin: 1rem 0 0 0;
  font-size: 1.2rem;
  line-height: 1.8rem;
  color: $colorLightest;
}

.button {
  @include resetButton;

  position: absolute;
  top: 22rem;
  z-index: 1;

  &::before {
    content: "";
    position: absolute;
    top: -1rem;
    left: -1rem;
    width: calc(100% + 1rem * 2);
    height: calc(100% + 1rem * 2);
  }

  &.buttonNext {
    left: calc(5rem + 27.5rem);

    .icon {
      transform: rotate(180deg);
    }
  }

  &:not(.buttonNext) {
    left: 3rem;
  }

  .icon {
    width: 1.1rem;
    fill: $colorLightest;
  }
}

.buttonDisabled {
  opacity: 0.5;
}
