@import "styles/variables";
@import "styles/mixins";

.header {
  position: absolute;
  left: 0;
  width: 100%;
  transition-duration: $transitionDuration;
  transform: translateY(0);
  transition-property: opacity, transform, top;
  z-index: 10;
}

.wrapper {
  display: flex;
  align-items: center;
  padding-top: 1.3rem;
  padding-bottom: 1.3rem;
}

.phone {
  @include navigation;
  @include linkBehavior;
}
