@import "variables";

// font mixins
@mixin h0($color: $colorDarkest) {
  font-family: "Play", Arial, sans-serif;
  font-size: 10rem;
  font-weight: 700;
  line-height: 15rem;
  color: $color;


  @media (max-width: $adaptiveMax) {
    font-size: 3.6rem;
    line-height: 5.4rem;
  }
}

@mixin h1($color: $colorDarkest) {
  font-family: "Play", Arial, sans-serif;
  font-size: 8rem;
  font-weight: 700;
  line-height: 9.2rem;
  color: $color;
}

@mixin h2($color: $colorDarkest) {
  font-family: "Play", Arial, sans-serif;
  font-size: 5rem;
  font-weight: 700;
  line-height: 7.5rem;
  color: $color;

  @media (max-width: $adaptiveMax) {
    font-size: 1.8rem;
    line-height: 2.7rem;
  }
}

@mixin timeline($color: $colorDarkest) {
  font-family: "Play", Arial, sans-serif;
  font-size: 4rem;
  font-weight: 700;
  line-height: 6rem;
  color: $color;

  @media (max-width: $adaptiveMax) {
    font-size: 1.8rem;
    line-height: 2.7rem;
  }
}

@mixin h3($color: $colorDarkest) {
  font-family: "Play", Arial, sans-serif;
  font-size: 3.6rem;
  font-weight: 700;
  line-height: 4.2rem;
  color: $color;

  @media (max-width: $adaptiveMax) {
    font-size: 1.8rem;
    line-height: 2.1rem;
  }
}

@mixin h4($color: $colorDarkest) {
  font-family: "Play", Arial, sans-serif;
  font-size: 2.4rem;
  font-weight: 700;
  line-height: 3.6rem;
  color: $color;
}

@mixin navigation($color: $colorDarkest) {
  font-family: "Play", Arial, sans-serif;
  font-size: 2.4rem;
  font-weight: 400;
  line-height: 2.8rem;
  color: $color;

  @media (max-width: $adaptiveMax) {
    font-size: 1.8rem;
    line-height: 2.1rem;
  }
}

@mixin regularBanner($color: $colorDarkest, $weight: 300) {
  font-family: "Montserrat", Arial, sans-serif;
  font-size: 2.4rem;
  font-weight: $weight;
  line-height: 3.6rem;
  color: $color;


  @media (max-width: $adaptiveMax) {
    font-size: 1.2rem;
    line-height: 1.8rem;
  }
}

@mixin logo {
  font-family: "Play", Arial, sans-serif;
  font-size: 2.1rem;
  font-weight: 700;
  line-height: 1;


  @media (max-width: $adaptiveMax) {
    font-size: 1.4rem;
  }
}

@mixin lang {
  font-family: "Play", Arial, sans-serif;
  font-size: 1.8rem;
  font-weight: 400;
  line-height: 2.1rem;
  color: $colorDarkest;

  @media (max-width: $adaptiveMax) {
    font-size: 1.2rem;
    line-height: 1.3rem;
  }
}

@mixin regular ($color: $colorDarkest, $weight: 300) {
  font-family: "Montserrat", Arial, sans-serif;
  font-size: 1.8rem;
  font-weight: $weight;
  line-height: 2.7rem;
  color: $color;

  @media (max-width: $adaptiveMax) {
    font-size: 1rem;
    line-height: 1.5rem;
  }
}

@mixin regularSmall ($color: $colorDarkest, $weight: 300) {
  font-family: "Montserrat", Arial, sans-serif;
  font-size: 1.6rem;
  font-weight: $weight;
  line-height: 2.4rem;
  color: $color;


  @media (max-width: $adaptiveMax) {
    font-size: 1rem;
    line-height: 1.5rem;
  }
}


// reset mixins

@mixin resetButton {
  margin: 0;
  padding: 0;
  font-size: 0;
  background-color: transparent;
  border: none;
  box-shadow: none;
  border-radius: 0;
  cursor: pointer;
}

@mixin resetList {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin resetFieldset {
  margin: 0;
  padding: 0;
  border: none;
}

// style mixins
@mixin linkBehavior {
  transition-duration: $transitionDuration;
  text-decoration: none;

  &:hover {
    text-decoration: underline;
  }
}

@mixin shadowStandard {
  box-shadow: -0.7rem 0.7rem 0.8rem rgba(0, 0, 0, 0.15);
}

@mixin visuallyHidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
  font-size: 0;
}

@mixin overlay {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $colorOverlay;
    z-index: 1;
    mix-blend-mode: hard-light;
  }
}

@mixin decoratedItem {
  position: relative;
  padding-left: 2.4rem;

  &::before {
    content: "";
    position: absolute;
    top: calc(50% - 1.7rem / 2);
    left: 0;
    width: 1.7rem;
    height: 1.7rem;
    border: 0.4rem solid $colorHighlight;
    border-radius: 50%;
  }
}