@import "styles/variables";
@import "styles/mixins";

.list {
  @include resetList;

  display: flex;
  align-items: center;
}

.item {
  &:not(:first-child) {
    margin-left: 9rem;
  }
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    height: 100%;
    background-color: $colorDarkest;
  }

  .list {
    justify-content: center;
    flex-direction: column;
    height: 100%;
  }


  .item {
    &:not(:first-child) {
      margin-top: 5.5rem;
      margin-left: 0;
    }
  }
}