@import "styles/variables";

.wrapper {
  --menu-width: 19rem;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  transition-duration: $transitionDuration;
  z-index: 10;
}

.menu {
  position: relative;
  width: var(--menu-width);
  height: 100%;
}

.wrapperHidden {
  transform: translateX(-19rem);
}

.wrapperVisible {
  transform: translateX(0);
}