@import "styles/variables";

.wrapper {
  .heading {
    padding-right: 70%;
  }

  .list {
    position: relative;
    margin-top: 10rem;

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: $colorHighlight;
    }
  }
}


@media (max-width: $adaptiveMax) {
  .wrapper {
    .heading {
      padding-right: 0;
      width: 15rem;
    }

    .list {
      margin-top: 3rem;

      &::before {
        display: none;
      }
    }

    .item {
      &:not(:first-child) {
        margin-top: 3rem;
      }
    }
  }
}