@import "styles/variables";

.section {
  background-color: lightgray;
  background-image: url("/assets/images/banner__bg.jpg");
  background-size: cover;

  .wrapper {
    display: grid;
    grid-template-columns: 70rem 1fr;
    grid-column-gap: 20rem;
    height: 80rem;
  }

  .info {
    position: relative;
    padding-top: 23.5rem;
    z-index: 2;
  }

  .description {
    margin-top: 4.6rem;
  }

  .sideblock {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    height: auto;

    .heading {
      display: none;
    }

    .info {
      order: 2;
      padding-top: 0;
      padding-bottom: 3rem;
    }

    .description {
      margin-top: 5rem;
      text-align: center;
    }

    .sideblock {
      order: 1;
      margin-top: 0;
    }
  }
}