@import "styles/mixins";
@import "styles/variables";

.wrapper {
  @include resetList;

  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  grid-gap: 2rem 4rem;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    display: flex;
    flex-direction: column;
    grid-gap: 0;
  }
}