@import "styles/mixins";
@import "styles/variables";

.list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.item {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
}

.button {
  @include resetButton;
  @include regularSmall($colorHighlight);

  font-size: 1.2rem;
  text-align: center;
}