@import "styles/variables";

.section {
  padding-top: 7rem;
  padding-bottom: 6rem;
  border-top: 1px solid $colorDarkest;

  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: var(--padding-inner);
  }

  .list {
    margin-top: 7rem;
  }

  .button {
    margin: 4rem auto 0 auto;
  }
}

@media (max-width: $adaptiveMax) {
  .section {
    padding-top: 3rem;
    padding-bottom: 3rem;

    .header {
      padding-left: 0;
    }

    .list {
      margin-top: 2.5rem;
      margin-right: -3rem;
      margin-left: -3rem;
    }

    .button {
      margin: 2.5rem auto 0 auto;
    }
  }
}