.wrapper {
  margin-top: 5rem;
  padding-top: 2.5rem;
  padding-bottom: 3.5rem;
}

.heading {
  padding-left: 11.8rem;
  width: 50rem;
}

.description {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem 7rem;
  margin-top: 2.5rem;
  width: 130rem;

  & > *:last-child {
    grid-column: 1 / -1;
  }
}
