@import "styles/variables";
@import "styles/mixins";

.wrapper {
  position: relative;

  :global {
    .swiper-wrapper {
      @include resetList;
    }
  }
}

.button {
  position: absolute;
  bottom: 37.5rem;
  z-index: 1;

  &:not(.buttonNext) {
    left: calc(50% - 192.45rem / 2 + 15rem);
  }

  &.buttonNext {
    right: calc(50% - 192.45rem / 2 + 15rem);
  }
}

@media (max-width: $adaptiveMax) {
  .button {
    bottom: calc(50% - 2.4rem / 2 - 1rem);


    &:not(.buttonNext) {
      left: 2rem;
    }

    &.buttonNext {
      right: 2rem;
    }
  }
}