@import "styles/variables";

.wrapper {
  --content-padding-left: 82rem;
  display: grid;
  grid-template-columns: 82rem minmax(0, 1fr);
  align-items: flex-start;
}

.heading,
.description,
.controls {
  grid-column: 2 / 3;
}

.description {
  margin-top: 4rem;
  padding-right: 13rem;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    --content-padding-left: 0;
    display: flex;
    flex-direction: column;
  }

  .heading,
  .description,
  .controls {
    align-self: center;
    text-align: center;
  }

  .description {
    margin-top: 1.5rem;
    padding-right: 0;
    padding-left: 0;
  }
}