@import "styles/variables";
@import "styles/mixins";

.wrapper {
  --color: black;
  position: relative;
  display: grid;
  grid-template-columns: 65rem 2rem 1fr;

  &:not(.wrapperOdd) {
    &::before {
      content: "";
      position: absolute;
      left: 100%;
      top: 0;
      width: 50vw;
      height: 100%;
      box-shadow: -0.7rem 0.7rem 0.8rem rgba(0, 0, 0, 0.15);
    }

    &::after {
      content: "";
      position: absolute;
      left: calc(100% - 1.5rem);
      top: 0;
      width: 3rem;
      height: 100%;
      background-color: $colorLightest;
    }

    .description {
      box-shadow: -0.7rem 0.7rem 0.8rem rgba(0, 0, 0, 0.15);
    }
  }
}

.img {
  grid-column: 1 / 3;
  grid-row: 1 / 2;
  position: absolute;
  top: 0;
  left: 0;
  padding: 3rem 0;
  width: 120%;
  height: 100%;
  object-fit: cover;
  object-position: left center;
  z-index: 1;
}

.description {
  grid-column: 2 / 4;
  grid-row: 1 / 2;
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 3.5rem 5rem;
  min-height: 40rem;
  border-top-left-radius: 3rem;
  border-bottom-left-radius: 5rem;
  background-color: white;
  z-index: 3;
}

.details {
  @include resetList;

  display: grid;
  grid-template-columns: 2fr 2fr 1fr;
  grid-column-gap: 3rem;
  margin-top: 1.7rem;
}

.detail {
  @include regular(var(--color), 500);

  display: flex;
  align-items: center;
}

.icon {
  margin-right: 1rem;
  fill: var(--color);
}

.iconLocation {
  width: 2.4rem;
}

.iconTimeline {
  width: 2.9rem;
}

.list {
  @include resetList;

  display: flex;
  flex-direction: column;
  margin-top: 1.6rem;
}

.item {
  display: grid;
  grid-template-columns: max-content 1fr;
  align-items: flex-start;
;
  color: var(--color);

  &:not(:first-child) {
    margin-top: 1.1rem;
  }
}

.key,
.value {
  @include regular;

  color: var(--color);
}

.key {
  width: max-content;
}

.value {
  margin: 0 0 0 0.5rem;

  font-weight: 500;
}

.wyziwig {
  margin-top: 2rem;
  margin-bottom: 2rem;

  p {
    @include regularSmall;

    margin: 0;
    color: var(--color);

    &:not(:first-child) {
      margin-top: 2rem;
    }
  }

  ul {
    @include resetList;

    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 1.2rem 5rem;
    margin-top: 3rem;
  }

  li {
    @include regularSmall(var(--color), 500);

    position: relative;
    padding-left: 2rem;

    &::before {
      content: "";
      position: absolute;
      top: 0.8rem;
      left: 0;
      width: 0.7rem;
      height: 0.7rem;
      background-color: var(--color);
      border-radius: 50%;
    }
  }
}

.link {
  @include regular($colorDarkest, 600);

  display: inline-flex;
  align-items: center;
  margin-top: auto;

  &:hover {
    .iconArrow {
      margin-left: 2rem;
    }
  }
}

.iconArrow {
  margin-left: 1rem;
  width: 2rem;
  fill: $colorDarkest;
  transition-duration: $transitionDuration;
  transition-property: margin-left;
}

.wrapperOdd {
  --color: white;

  &::before {
    content: "";
    position: absolute;
    left: 100%;
    top: 0;
    width: 50vw;
    height: 100%;
    background-color: $colorHighlightLight;
  }

  .description {
    background-color: $colorHighlightLight;
  }

  .link {
    color: $colorLightest;

    .iconArrow {
      fill: $colorLightest;
    }
  }
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    display: flex;
    flex-direction: column;


    &:not(.wrapperOdd) {
      &::before,
      &::after {
        display: none;
      }

      .description {
        box-shadow: -0.7rem 0.7rem 0.8rem rgba(0, 0, 0, 0.15);
      }

      .wyziwig {
        p, li {
          color: $colorDarkest;
        }
      }
    }
  }

  .wrapperOdd {
    &::before {
      display: none;
    }
  }

  .img {
    margin: 0;
    height: 34rem;
  }

  .description {
    padding: 1.5rem 3rem 3rem 3rem;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    z-index: 1;
  }

  .wyziwig {
    p, li {
      @include regular($colorLightest);
    }
  }
}