  display: flex;
  align-items: center;
  color: $colorDarkest;
}

.logo {
  margin-right: 3rem;
  width: 8.5rem;
  fill: $colorDarkest;
  stroke: $colorDarkest;
}

.logoSingle {
  margin-right: 0;
  width: 100%;
}
