@import "styles/variables";

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 3rem 5.5rem;

  &:hover {
    .logo {
      filter: grayscale(0);
    }
  }
}

.logo {
  width: 100%;
  max-height: 14rem;
  filter: grayscale(1);
  transition-duration: $transitionDuration;
  object-fit: contain;
  pointer-events: none;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    padding: 1rem 1.5rem;
    height: 14rem;
  }
}