@import "styles/variables";

.wrapper {
  padding: 4rem 8rem 7rem var(--global-padding);
  background-color: $colorMedium;
  border-top-right-radius: 5rem;
  border-bottom-right-radius: 5rem;
}

.description {
  margin-top: 3rem;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    padding: 3.5rem 6.5rem 1rem 6.5rem;
    text-align: center;
    background-color: transparent;
    border-radius: 0;
  }

  .description {
    margin-top: 0.8rem;
  }
}