@import "styles/variables";

.header {
  position: absolute;
  width: 100%;
  z-index: 10;

  &HiddenFixed {
    position: fixed;
    top: -100%;
  }

  &HiddenFixedTransform {
    position: fixed;
    transform: translateY(-100%);
  }

  &VisibleFixed {
    position: fixed;
    top: 0;
    transform: translateY(0);
    background-color: $colorLightest;
  }

  &HiddenAbsolute {
    position: absolute;
    transform: translateY(-100%);
  }
}
