@import "styles/mixins";
@import "styles/variables";

.section {
  border-top: 1px solid $colorDarkest;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 80vh;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    right: calc(50% - 55rem);
    height: 70vh;
    width: 12.5rem;
    background-image: url("/assets/images/404.png");
    background-size: contain;
    background-repeat: no-repeat;
  }
}

.code {
  @include h0;
}

.description {
  @include h1;

  font-size: 7rem;
  line-height: 8rem;
  text-align: center;
}

.button {
  margin-top: 3rem;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    &::before {
      top: calc(50% + 20rem);
      right: auto;
      left: 0;
      height: 7rem;
      width: 50rem;
      background-image: url("/assets/images/404adaptive.png");
    }
  }

  .code {
    font-size: 6rem;
    line-height: 7rem;
  }

  .description {
    padding: 0 10%;
    font-size: 4rem;
    line-height: 6rem;
  }
}