@import "styles/variables";

.wrapper {
  position: relative;
  padding: 0 15rem;
}

.slider {
  margin-top: 2rem;
}

.button {
  position: absolute;
  bottom: 10rem;

  &:not(.buttonNext) {
    left: 0;
  }

  &.buttonNext {
    right: 0;
  }
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    padding: 0;

  }

  .slider {
    margin-right: -3rem;
    margin-left: -3rem;
    padding-left: 3rem;
  }
}