@import "styles/variables";

.wrapper {
  display: grid;
  grid-template-columns: 68rem 1fr;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: $colorMedium;
  }
}
