@import "styles/variables";
@import "styles/mixins";

.content {
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 100%;
  z-index: 2;
}

.heading {
  @include h3($colorLightest);

  text-transform: uppercase;
}

.duration {
  @include h0($colorLightest);

  margin-left: 4rem;
  letter-spacing: 1rem;
}

@media (max-width: $adaptiveMax) {
  .content {
    justify-content: flex-start;
  }

  .heading {
    margin: 0;
    padding-top: 0.5rem;
  }

  .duration {
    margin-left: 2rem;
    letter-spacing: 0.2rem;
  }
}