@import "styles/variables";
@import "styles/mixins";

.wrapper {
  display: grid;
  grid-template-columns: 50rem 1fr;
  align-items: center;
  padding: 0;
  width: 120rem;
  background-color: $colorLightest;
}

.image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.filter {
  margin: 5rem;
}

.filter + .list {
  .item {
    &:first-child {
      margin-top: 2.5rem;
    }
  }
}

.list {
  display: flex;
  flex-direction: column;
  padding: 0 5rem;
  max-height: 50rem;
  overflow-y: scroll;
}

.item {
  &:first-child {
    margin-top: 10rem;
  }

  &:not(:first-child) {
    margin-top: 2.5rem;
  }

  &:last-child {
    margin-bottom: 10rem;
  }
}

.link {
  @include regular($colorHighlight);

  transition-duration: $transitionDuration;
  transition-property: color;

  &:hover {
    color: $colorDarkest;
  }
}

.icon {
  margin-right: 1rem;
  width: 5rem;
}

