@import "styles/variables";

.wrapper {
  padding-top: 6.5rem;
  padding-bottom: 12rem;
}

.list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 1rem;
  padding-left: 5rem;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    padding-top: 2rem;
  }

  .list {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin-top: 0;
    padding-left: 0;
  }
}

