@import "styles/variables";
@import "styles/mixins";

.wrapper {
  overflow: hidden;
}

.container {
  display: grid;
  grid-template-columns: 1fr 66.4rem;
}

.heading {
  grid-column: 1 / 2;
  grid-row: 1 / 2;
  padding-top: 7rem;
  padding-bottom: 5rem;
  padding-left: 14rem;
}

.frame {
  position: relative;
  grid-column: 2 / 3;
  grid-row: 1 / 3;
  border-top-right-radius: 5rem;
  border-top-left-radius: 5rem;
  overflow: hidden;
  z-index: 1;
  transition-duration: 0.6s;
  transition-timing-function: ease-out;
  transform: translateY(100%);
  transition-property: transform;
}

.image,
.video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.wyziwig {
  @include regular($colorLightest);

  position: relative;
  grid-column: 1 / 2;
  grid-row: 2 / 3;
  padding: 6rem 10rem 6rem 14rem;
  background-color: $colorHighlight;
  border-top-left-radius: 5rem;
  border-bottom-left-radius: 5rem;
  transition-duration: 0.6s;
  transition-property: transform;
  transition-timing-function: ease-out;
  transform: translateX(100vw);

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 100%;
    width: 50vw;
    height: 100%;
    background-color: $colorHighlight;
  }
}

.wrapperVisible {
  .frame {
    transform: translateY(0);
  }

  .wyziwig {
    transform: translateX(0);
  }
}

@media (max-width: $adaptiveMax) {
  .container {
    display: flex;
    flex-direction: column;
  }

  .heading {
    padding-top: 0;
    padding-bottom: 2rem;
    padding-left: 0;
  }

  .frame {
    margin-left: -3rem;
    width: 43rem;
    border-radius: 0 5rem 5rem 0;
    transform: translateX(-100%);
  }

  .wyziwig {
    margin-top: -10rem;
    padding: 2.2rem 1.2rem 2.4rem 3rem;
    background-color: $colorHighlight;
    border-top-left-radius: 5rem;
    border-bottom-left-radius: 5rem;
    transform: translateX(100%);
    z-index: 2;
  }

  .wrapperVisible {
    .frame {
      transform: translateX(0);
    }
  }
}