@import "styles/variables";
@import "styles/mixins";

.wrapper {
  color: $colorDarkest;
}

.heading {
  text-align: center;
}

.description,
.error {
  @include regular;

  margin: 1.6rem 0 0 0;
  text-align: center;
}

.error {
  font-weight: 700;
}

.controls {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 3rem;
  margin-top: 2.4rem;
}

.button {
  width: 100%;
}

.buttonCentered {
  grid-column: 1 / -1;
  margin: 0 auto;
  width: 25rem;
}
