@import "styles/mixins";
@import "styles/variables";

.button {
  @include resetButton;

  position: relative;
}

.buttonSlider {
  padding: 1rem;
  width: 4.2rem;
  height: 4.2rem;
  background-color: $colorLightest;
  border-radius: 50%;
  transition-duration: $transitionDuration;
  transition-property: opacity, color, background-color, box-shadow;
  box-shadow: 0 0.5rem 1rem transparent;

  &:hover,
  &:active {
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.5);
  }
}

.iconArrow {
  width: 100%;
  fill: $colorHighlightLight;
}

.buttonSliderNext {
  .iconArrow {
    transform: rotate(180deg);
  }
}

.buttonSliderDisabled {
  opacity: 0.5;
  pointer-events: none;
}

.buttonSliderDark {
  background-color: $colorHighlight;

  .iconArrow {
    fill: $colorLightest;
  }

  &:active {
    background-color: $colorHighlight;
  }
}

.buttonFilled {
  @include lang;

  padding: 0 3.5rem;
  height: 6rem;
  font-weight: 700;
  color: $colorLightest;
  text-transform: uppercase;
  background-color: $colorHighlight;
  border-radius: 1rem;
  transition-duration: $transitionDuration;
  transition-property: box-shadow, color, background-color;

  &:hover,
  &:active {
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.5);
  }

  &:active {
    background-color: $colorHighlightLight;
  }
}

.buttonBordered {
  @include lang;

  padding: 0 3.5rem;
  height: 6rem;
  font-weight: 700;
  color: $colorHighlight;
  text-transform: uppercase;
  background-color: $colorLightest;
  border: 1px solid $colorHighlight;
  border-radius: 1rem;
  transition-duration: $transitionDuration;
  transition-property: box-shadow, color, background-color;

  &:hover,
  &:active {
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.5);
  }

  &:active {
    background-color: $colorHighlightLight;
  }
}

.buttonArrow {
  @include lang;

  display: inline-flex;
  justify-content: center;
  align-items: center;

  &:hover,
  &:active {
    .wrapperArrow {
      box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.5);
    }
  }

  &:active {
    .wrapperArrow {
      background-color: $colorHighlightLight;
    }
  }
}

.wrapperArrow {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin-left: 1.8rem;
  width: 2.4rem;
  height: 2.4rem;
  background-color: $colorHighlight;
  border-radius: 50%;
  transition-duration: $transitionDuration;
  transition-property: box-shadow, color, background-color;

  .iconArrow {
    position: relative;
    width: 1.5rem;
    fill: $colorLightest;
  }
}

.buttonArrowReverse {
  .wrapperArrow {
    order: 1;
    margin-right: 1.8rem;
    margin-left: 0;

    .iconArrow {
      transform: rotate(180deg);
    }
  }

  .label {
    order: 2;
  }
}

.buttonMore {
  @include regular($colorHighlight, 500);

  display: flex;
  align-items: center;

  .iconLoad {
    margin-right: 0.7rem;
    width: 2.2rem;
    fill: $colorHighlight;
  }

  &:hover {
    .iconLoad {
      animation-name: rotate;
      animation-duration: 1s;
      animation-fill-mode: both;
      animation-iteration-count: infinite;
      animation-timing-function: linear;
    }
  }

  @keyframes rotate {
    0% {
      transform: rotate(0);
    }

    100% {
      transform: rotate(360deg);
    }
  }
}

.buttonEdit {
  @include lang;

  display: flex;
  align-items: center;
  line-height: 3rem;
  transition-duration: $transitionDuration;
  transition-property: color;

  .iconEdit {
    margin-left: 0.7rem;
    width: 1.7rem;
    fill: $colorDarkest;
    transition-duration: $transitionDuration;
    transition-property: fill;
  }

  &:hover {
    color: $colorHighlight;

    .iconEdit {
      fill: $colorHighlight;
    }
  }
}

.buttonLogin {
  @include lang;

  .spinner {
    display: none;
  }
}

.iconUser {
  margin-right: 0.6rem;
  width: 1.4rem;
  fill: $colorDarkest;
}

.buttonLoading {
  pointer-events: none;
}

.spinner {
  position: absolute;
  top: calc(50% - 2.5rem / 2);
  right: 0.5rem;
  width: 2rem;
  stroke: $colorLightest;
}

.buttonClose {
  &::before {
    content: "";
    position: absolute;
    top: -1rem;
    left: -1rem;
    width: calc(100% + 2rem);
    height: calc(100% + 2rem);
  }

  .iconClose {
    width: 1.4rem;
    fill: $colorLightest;
  }
}

.buttonBurger {
  &::before {
    content: "";
    position: absolute;
    top: -0.5rem;
    left: -0.5rem;
    width: calc(100% + 1rem);
    height: calc(100% + 1rem);
  }

  .iconBurger {
    width: 1.6rem;
    fill: $colorDarkest;
  }
}