@import "styles/variables";

.section {
  padding-top: 8rem;
  padding-bottom: 8rem;
  border-top: 1px solid $colorDarkest;

  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .imageInput {
    margin-top: 4rem;
  }

  .block {
    margin-top: 4rem;
  }

  .footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 4rem;
  }

  .buttonCancel {
    margin-left: auto;
    margin-right: 2rem;
  }
}