.wrapper {
  position: relative;
  padding-bottom: 8rem;
  overflow: hidden;

  &::before {
    content: "";
    position: absolute;
    right: -10rem;
    top: 0;
    width: 137rem;
    height: 69.5rem;
    background-image: url("/assets/images/principles.jpg");
    background-size: cover;
    z-index: -1;
  }
}
