@import "mixins";
@import "fonts";
@import "variables";

html {
  font-size: 10px;
}

body {
  margin: 0;
  padding: 0;
  overflow: auto;


  &::-webkit-scrollbar {
    width: 0.5rem;
  }

  &::-webkit-scrollbar-thumb {
    background-color: gray;
  }
}

#root {
  overflow: hidden;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  outline: none;
  scrollbar-width: thin;
  scrollbar-color: gray transparent;
}

a {
  text-decoration: none;
}

.visuallyHidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
  font-size: 0;
}

.ReactCollapse--collapse {
  transition: height 500ms;
}

.grid {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

:where(ul) {
  @include resetList;
}