// screen sizes
$adaptiveMax: 1200px;

// colors
$colorDarkest: rgb(2, 9, 25);
$colorMediumLight: rgb(196, 196, 196);
$colorMedium: rgb(225, 225, 225);
$colorLightest: rgb(255, 255, 255);
$colorHighlight: rgb(0, 84, 170);
$colorIcon: rgb(1, 60, 121);
$colorOverlay: rgba(41, 136, 233, 0.7);
$colorHighlightLight: rgb(41, 136, 233);

// transitions
$transitionDuration: 0.3s;

// spaces
body {
  :global {
    --global-padding: 15rem;
    --padding-inner: 13rem;
  }
}
