@import "styles/variables";
@import "styles/mixins";

.wrapper {
  position: relative;
  display: block;
}

.input {
  @include regular($colorDarkest);

  padding-right: 2.5rem;
  padding-left: 2.5rem;
  height: 5rem;
  width: 100%;
  border: 1px solid $colorMediumLight;
  border-radius: 1rem;
  box-shadow: none;
  outline: none;
  transition-duration: $transitionDuration;

  &:hover {
    border-color: $colorHighlight;
  }

  &:focus {
    border-color: $colorHighlight;
  }
}

.inputSearch {
  .input {
    padding-right: 5rem;
    padding-left: 2.7rem;

    &:focus + .iconSearch {
      fill: $colorOverlay;
    }
  }
}

.iconSearch {
  position: absolute;
  bottom: 1.2rem;
  right: 1.2rem;
  width: 2.6rem;
  fill: $colorIcon;
  transition-duration: $transitionDuration;
}