@import "styles/mixins";
@import "styles/variables";

.link {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  transition-duration: $transitionDuration;
  transition-property: background-color;

  &:hover,
  &:active {
    background-color: $colorMedium;
  }
}

.frame {
  width: 100%;
  height: 30rem;
}

.image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.content {
  padding: 2.5rem 4rem;
}

.date {
  @include regular;
  @include decoratedItem;
}

.heading {
  @include regularBanner($colorDarkest, 500);

  margin-top: 1rem;
}

.description {
  @include regularSmall;

  margin: 0.3rem 0 0 0;
}

@media (max-width: $adaptiveMax) {
  .wrapperOdd {
    .content {
      background-color: $colorMedium;
    }
  }

  .content {
    padding: 2rem 6rem 3rem 6rem;
  }

  .heading {
    @include h2;

    font-weight: 500;
  }
}