@import "styles/variables";
@import "styles/mixins";

.list {
  display: flex;
  flex-direction: column;
}

.item {
  &:not(:first-child) {
    position: relative;
    margin-top: 0.7rem;

    &::before {
      content: "";
      position: absolute;
      top: -0.35rem;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: $colorDarkest;
    }
  }
}

.button {
  @include resetButton;
  @include lang;
}

.buttonActive {
  font-weight: 700;
  pointer-events: none;
}
