@import "styles/variables";
@import "styles/mixins";

.footer {
  @include regular;

  display: flex;
  align-items: center;
}

.list,
.item {
  display: flex;
  align-items: center;
}

.item {
  flex-direction: column;
  align-items: flex-start;

  &:not(:first-child) {
    margin-left: 12rem;
  }
}

.copyright {
  margin-left: auto;
}

@media (max-width: $adaptiveMax) {
  .footer {
    font-size: 0.8rem;
  }

  .item {
    &:not(:first-child) {
      margin-left: 3rem;
    }
  }
}