@import "styles/variables";

.wrapper {
  position: relative;
  padding-top: 5rem;
  padding-bottom: 20rem;

  &::before {
    content: "";
    position: absolute;
    top: -9rem;
    left: 15rem;
    width: 54.8rem;
    height: 56rem;
    background-image: url("/assets/images/trafo_outline.png");
    background-size: cover;
    z-index: 1;
  }

  &::after {
    content: "";
    position: absolute;
    top: 4rem;
    left: 40rem;
    width: 31.3rem;
    height: 31.3rem;
    background-image: url("/assets/images/trafo_outline_mask.png");
    background-size: cover;
  }
}

.icon {
  position: absolute;
  top: 4rem;
  left: 40rem;
  width: 31.3rem;
  fill: $colorHighlight;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    padding-bottom: 20rem;


    &::before {
      top: auto;
      bottom: 3rem;
      left: calc(50% -  17rem / 2);
      width: 17rem;
      height: 18rem;
    }

    &::after {
      top: auto;
      bottom: 6.7rem;
      left: auto;
      right: calc(50% - 10rem / 2 - 4.5rem);
      width: 10rem;
      height: 10rem;
    }
  }

  .icon {
    top: auto;
    bottom: 7rem;
    right: calc(50% - 10rem / 2 - 7rem);
    left: auto;
    width: 10rem;
  }
}