@import "styles/variables";
@import "styles/mixins";

.label {
  @include regular($colorHighlight, 500);

  display: inline-flex;
  align-items: center;
  cursor: pointer;
  transition-duration: $transitionDuration;
  transition-property: color;

  &:hover {
    color: $colorDarkest;

    .icon {
      fill: $colorDarkest;
    }
  }
}

.icon {
  margin-right: 1.2rem;
  width: 2.4rem;
  fill: $colorHighlight;
  transition-duration: $transitionDuration;
  transition-property: fill;
}