@import "styles/variables";
@import "styles/mixins";

.wrapper {
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  padding-bottom: 7.4rem;
  width: 25rem;
  z-index: 1;

  &::before {
    content: "";
    position: absolute;
    bottom: -1.5rem;
    right: 5rem;
    width: 3rem;
    height: 3rem;
    background-color: $colorHighlight;
    border-radius: 50%;
    transition-duration: $transitionDuration;
    z-index: 1;
  }

  &::after {
    content: "";
    position: absolute;
    top: 5rem;
    right: 0;
    width: 19.6rem;
    height: 19.6rem;
    border: 1px solid $colorHighlight;
    border-radius: 50%;
    z-index: 5;
  }

  &:hover {
    &::before {
      background-color: $colorLightest;
    }

    .description {
      transform: translateY(0);
    }
  }
}

.image {
  position: absolute;
  bottom: 0;
  left: -9rem;
  width: 100%;
  z-index: 3;
}

.label {
  @include timeline;

  span {
    color: $colorHighlight;
  }
}

.frame {
  position: absolute;
  top: calc(100% - 1px);
  left: 0;
  width: 45rem;
  height: 50rem;
  clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
  z-index: 6;
  pointer-events: none;
}

.description {
  @include regular($colorLightest);

  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
  padding: 2rem;
  width: 100%;
  background-color: $colorHighlight;
  border-radius: 2rem;
  transform: translateY(-105%);
  transition-duration: $transitionDuration;
  box-shadow: 0.4rem 0.4rem 0.4rem rgba(0, 0, 0, 0.15);

  ul {
    @include resetList;
  }

  li {
    position: relative;
    padding-left: 2.6rem;

    &::before {
      --size: 1.2rem;
      content: "";
      position: absolute;
      top: 1rem;
      left: 0;
      width: var(--size);
      height: var(--size);
      background-color: $colorLightest;
      border-radius: 50%;
    }

    &:not(:first-child) {
      margin-top: 1.5rem;
    }
  }
}

.overlay {
  display: none;
}

@media (min-width: $adaptiveMax) {
  .wrapperLast {
    .image {
      left: auto;
      right: 9rem;
    }

    .frame {
      left: auto;
      right: 0;
    }
  }
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    padding-bottom: 0;
    width: 16rem;
    height: 8rem;

    &::before {
      bottom: -1rem;
      right: -2rem;
      left: auto;
      width: 2rem;
      height: 2rem;
    }

    &::after {
      top: 2.5rem;
      right: -1rem;
      width: 12rem;
      height: 12rem;
    }

    &:not(.wrapperEven) {
      .overlay {
        top: 100%;
        border-radius: 0 2rem 2rem 0;
      }
    }
  }

  .frame {
    width: 27rem;
    right: auto;
    left: 0;
  }

  .image {
    position: relative;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  .label {
    position: absolute;
    top: 3rem;
    right: -5rem;
    left: auto;
  }

  .wrapperEven {
    margin-left: auto;


    &::before {
      right: auto;
      left: -2rem;
    }

    &::after {
      top: 2.5rem;
      right: auto;
      left: -1rem;
    }

    .label {
      right: auto;
      left: -5rem;
    }

    .frame {
      right: 0;
      left: auto;
    }

    .overlay {
      top: calc(100% - 2px);
      right: 3rem;
      left: auto;
      height: calc(11rem + 2px);
      border-right: none;
      border-left: 2px solid $colorHighlight;
      border-radius: 2rem 0 0 2rem ;
    }
  }

  .wrapperFirst {
    .overlay {
      right: 3rem;
      width: calc(100vw - 3rem * 2 - 3rem);
      top: 100%;
      border-radius: 2rem 0 0 2rem;
    }
  }

  .wrapperLast {
    &.wrapperEven {
      .overlay {
        right: 3rem;
        width: calc(100vw - 3rem * 2 - 3rem);
        top: calc(100% - 2px);
        border-left: none;
        border-bottom: none;
        border-radius: 0 0 0 0;
      }
    }

    &:not(.wrapperEven) {
      .overlay {
        top: calc(100% - 2px);
        border-right: none;
        border-bottom: none;
        border-radius: 0 0 2rem 0;
      }
    }
  }

  .overlay {
    position: absolute;
    right: auto;
    left: 3rem;
    display: block;
    width: calc(100vw - 3rem * 2 - 3rem);
    height: 11rem;
    border-top: 2px solid $colorHighlight;
    border-right: 2px solid $colorHighlight;
    border-bottom: 2px solid $colorHighlight;
    border-left: none;
    pointer-events: none;
  }
}