@import "../../../styles/variables";
@import "../../../styles/mixins";

.wrapper {
  position: relative;
  height: 25rem;
  overflow: hidden;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2);
    z-index: 1;
  }
}

.overlay {
  position: relative;
  height: 70rem;
  background-size: cover;
  background-position: center;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    height: 16rem;
  }
}