@import "styles/variables";
@import "styles/mixins";

.wrapper {
  display: grid;
  grid-template-columns: 90rem 30rem auto;
  justify-content: center;
  align-items: center;
  padding-top: 8rem;
  padding-bottom: 8rem;
}

.map,
.contacts {
  grid-row: 1 / 2;
}

.map {
  grid-column: 1 / 3;
  margin-left: auto;
  width: 100rem;
  height: 50rem;
}

.contacts {
  @include shadowStandard;

  grid-column: 2 / -1;
  position: relative;
  padding: 5rem 5rem 5rem 14rem;
  background-color: $colorLightest;
  border-radius: 5rem;
}

.list {
  margin-top: 4rem;
}

.item {
  @include regularBanner;

  position: relative;

  &:not(:first-child) {
    margin-top: 4rem;
  }
}

.icon {
  position: absolute;
  top: 0;
  left: -5rem;
  width: 3.2rem;
  fill: $colorHighlight;
}

.subitem {
  &:not(:first-child) {
    margin-top: 2rem;
  }
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    display: flex;
    flex-direction: column;
    padding-top: 0;
    padding-bottom: 0;
  }

  .contacts {
    order: 1;
    width: 100%;
    padding: 3rem 4.5rem 5rem 4.5rem;
    box-shadow: none;
  }

  .map {
    order: 2;
    width: 100%;
  }

  .list {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    grid-gap: 2rem 5rem;
    margin-top: 2rem;
    padding: 0 4rem;
  }

  .item {
    &:first-child {
      grid-column: 1 / -1;
    }

    &:not(:first-child) {
      margin-top: 0;
    }
  }

  .icon {
    width: 1.9rem;
    left: calc(-1.9rem - 2rem)
  }

  .sublist {
    display: flex;
    flex-direction: column;
  }

  .subitem {
    &:not(:first-child) {
      margin-top: 0;
    }
  }

}