@import "styles/variables";

.wrapper {
  position: relative;
  padding-top: 12rem;
  padding-bottom: 11rem;

  &::before {
    content: "";
    position: absolute;
    top: 15rem;
    left: calc(50% - 80rem);
    width: 35rem;
    height: 35rem;
    background-color: $colorHighlight;
    border-radius: 50%;
  }
}

.image {
  position: relative;
  grid-row: 1 / 6;
  width: 70rem;
}

.button {
  margin-top: 4rem;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    padding-top: 4rem;
    padding-bottom: 4rem;
  }

  .image {
    display: none;
  }
}