@import "styles/variables";
@import "styles/mixins";

.wrapper {
  --editor-padding-h: 2rem;
  --editor-padding-v: 1.5rem;
  position: relative;
  margin-top: 2.6rem;
  padding: var(--editor-padding-v) var(--editor-padding-h);
  background-color: $colorLightest;
  border: 1px solid $colorMediumLight;
  border-radius: 1rem;
  transition-duration: $transitionDuration;
  transition-property: border-color;

  &:hover,
  &:focus {
    border-color: $colorHighlight;
  }

  :global {
    .public-DraftEditorPlaceholder-inner {
      @include regular($colorDarkest, 500);

      position: absolute;
      top: calc(var(--editor-padding-v) + 0.8rem);
      left: var(--editor-padding-h);
    }
  }
}

.p {
  @include regular;

  margin: 1rem 0;
}

.icon {
  fill: red;
  width: 40%;
}

.h1 {
  @include h2;
}

.h2 {
  @include h3;
}

.h3 {
  @include h4;
}

.ul, .ol {
  @include regular;

  margin: 0 0 0 1.3rem;
}

.ul {
  list-style: initial;
}