@import "styles/mixins";
@import "styles/variables";

.button {
  @include resetButton;
  @include regular($colorHighlight);

  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 100%;
  width: max-content;
  border: 1px solid $colorHighlight;
  border-radius: 1rem;
  transition-duration: $transitionDuration;

  &:hover,
  &.buttonActive {
    color: $colorLightest;
    background-color: $colorHighlight;
    border-color: $colorHighlight;

    .icon {
      fill: $colorLightest;
    }
  }
}

.icon {
  width: 4rem;
  fill: $colorHighlight;
  transition-duration: $transitionDuration;
}
